import React, {Component} from 'react';
import '../../style/Deals.css';

const uuidv1 = require('uuid/v1');

class DealsTable extends Component {
    render () {
        var displayDrinks = [];
        var displaySides = [];
        var displayToppings = [];
        var displaySize = [];
        var displayCost = [];
        var allDrinks = [];
        if (this.props.deal.side !== 'na') {
            displaySides.push (
                <tr key={uuidv1()} ><td>Number of Sides:</td><td>{this.props.deal.side}</td></tr>
            )
        }
        if (this.props.deal.drink !== 'na'){
            this.props.deal.drink.forEach( drink => { 
                allDrinks.push (
                    <td key={uuidv1()} >{drink}</td>
                )}
            )
            displayDrinks.push( 
                <tr key={uuidv1()} >
                    <td>Drinks:</td>
                    {allDrinks}
                </tr>
            )
        }
        this.props.deal.toppings.forEach((topping) => {
            displayToppings.push (
                <td key={uuidv1()} >{topping}</td>
            )
        })
        this.props.deal.cost.forEach((cost) => {
            displayCost.push (
                <td key={uuidv1()} >{parseFloat(cost).toFixed(2)}</td>
            )
        })
        this.props.deal.size.forEach((size) => {
            displaySize.push (
                <td key={uuidv1()}  >{size}</td>
            )
        })
        return (
            <table>
                <tbody>
                    <th>
                       {this.props.deal.dealName} 
                    </th>
                    <tr>   
                        <td>Number of Pizzas:</td>
                        <td>{this.props.deal.quantity}</td>
                    </tr>
                    <tr>
                        <td>Sizes:</td>
                        {displaySize}
                    </tr>
                    <tr>
                        <td>Cost:</td>
                        {displayCost}
                    </tr>
                    <tr>
                        <td>Amount of Toppings:</td>
                        {displayToppings}
                    </tr>

                        {displayDrinks}
                        {displaySides}
                        <button onClick={(e) => {this.props.getDeal(this.props.deal)}}> Select Deal </button>
                </tbody>
            </table>
        )
    }
}

class DealDisplay extends Component {
    render () {
        var dealsTable = [];
        this.props.deals.forEach ((deal) => {
            dealsTable.push (
                <DealsTable deal = {deal} getDeal = {this.props.getDeal} key={uuidv1()} />
            )
        })
        return (
            <div className="dealDisplay">
                {dealsTable}
            </div>
        )
    }
}

