import React, {Component} from 'react';
import axios from 'axios';

import arrow from '../../icons/arrow.png';

import '../../style/Customer.css';

const uuidv1 = require('uuid/v1');

class CustomerRow extends Component {
    handleClick = (selectedCustomer) =>{ 
        this.props.onCustomerSelected(this.props.Customer);
    }
    render () {
        return (
            <tr className="customerBasicInfo">
                <td className="custPhone">{this.props.Customer.phone}</td>
                <td className="custName">{this.props.Customer.name}</td>
                <td className="custAddy">{this.props.Customer.address}</td>
                <td className="custSelect"><button onClick={(e)=> this.handleClick(this.props.Customer)}>Select</button></td>
            </tr>
        );
    }
}

class CustomerTable extends Component {
    render () {
        const filterText = this.props.filterText;
        const rows = [];
        var totalDisplayed = 0;
        this.props.customers.forEach((customer) => {
            if (customer.phone.indexOf(filterText) === -1) {
                return;
            }
            if (totalDisplayed < 100) {
                rows.push(
                    <CustomerRow
                    Customer={customer}
                    key={uuidv1()}
                    onCustomerSelected={this.props.onCustomerSelected}
                    />
                );
                totalDisplayed++;   
            }
        });
        if (filterText === '') {
            totalDisplayed = 0;
            return (
                <table className="CustomerTable">
                    <tbody></tbody>
                </table>
            )
        } else if (filterText !== '' && rows.length === 0) {
            return (
                <div>
                    ADD NEW
                </div>
            );
        } else {
            return (
                <table className="CustomerTable">
                    <tbody>{rows}</tbody>
                </table>
              );
        }
    }
}

class CustomerAdd extends Component {
    constructor() {
        super ();
        this.state ={
            createName : '',
            createAddress : '',
            createPhoneNumber : '',
            createError : '',
        }
    }
    onTextBoxChangeName = (event) => {
        this.setState({
            createName: event.target.value,
        }); 
        this.props.getName(event.target.value);
    }
    onTextBoxChangeAddress = (event) => {
        this.setState({
            createAddress: event.target.value,
        });
        this.props.getAddress(event.target.value);
    }
    onTextBoxChangePhoneNumber = (event) => {
        this.setState({
            createPhoneNumber: event.target.value,
        });
        this.props.getPhone(event.target.value);
    }
    render () {
        const {
            createName,
            createAddress,
            createPhoneNumber
        } = this.state;
        if (this.state.createName !== '' && (this.state.createPhoneNumber !== '' && this.state.createPhoneNumber.length === 10)) {
            this.props.isFullInput(true);
        } else this.props.isFullInput(false);
        return (
            <div className="createCustomer"> 
                <input 
                    type="text"
                    placeholder="Customer Name"
                    value={createName}
                    onChange={this.onTextBoxChangeName}
                />
                <input 
                    type="text"
                    placeholder="Address"
                    value={createAddress}
                    onChange={this.onTextBoxChangeAddress}
                />
                <input 
                    type="number"
                    placeholder="Phone Number"
                    value={createPhoneNumber}
                    onChange={this.onTextBoxChangePhoneNumber}
                />
            </div>
        )
    }
}

class OrderInfo extends Component {
    render () {
        var displayMain = [];
        var displayToppings = [];
        var displayMeat = [];
        var displayVeggies = [];
        var displayCheese = [];
        var displaySauce = [];
        var displayExtraType = [];
        if (this.props.item.type !== 'na') {
            displayMain.push (
                <tr key={uuidv1()}>
                    <td><h3><b>{this.props.item.type}</b></h3> </td>
                </tr>
            )
            if (this.props.item.toppings.meat !== []) {
                    this.props.item.toppings.meat.forEach( meats => {
                        displayMeat.push (
                            <td key={uuidv1()}> {meats} </td>
                        )
                })
            } else if (this.props.item.toppings.veggies !== []) {
                this.props.items.toppings.veggies.forEach ( veggies => {
                    displayVeggies.push (
                        <td key={uuidv1()}> {veggies} </td>
                    )
                })
            } else if (this.props.item.toppings.cheese !== []) {
                this.props.items.toppings.cheese.forEach ( cheese => {
                    displayCheese.push (
                        <td key={uuidv1()}> {cheese} </td>
                    )
                })
            } else if (this.props.item.toppings.sauce !== []) {
                this.props.items.toppings.sauce.forEach ( sauce => {
                    displaySauce.push (
                        <td key={uuidv1()}> {sauce} </td>
                    )
                })
            }
            displayToppings.push (
                <tr key={uuidv1()}>
                    <td>Toppings:</td>{displayMeat}{displayVeggies}{displayCheese}
                </tr>
            )
        } else if (this.props.item.extraType !== 'na') {
            displayExtraType.push (
                <tr key={uuidv1()}>
                    <td>Extra:</td> 
                    <td>{this.props.item.extraInfo}</td>
                    <td>{this.props.item.extraType}</td>
                </tr>
            )
        }
        return (
            <table>
                <tbody>
                    {displayMain}
                    {displayToppings}
                    {displayExtraType}
                </tbody>
            </table>
        )
    }
}

class DisplayPreviousOrders extends Component {
    render () {
        const table = [];
        this.props.fullOrder.orders.forEach ( item => {
            table.push (
                <OrderInfo item = {item} key={uuidv1()}/>
            )
        })
        return (
            <div className="previousOrdersTable">
                <p>{this.props.fullOrder.date}</p>
                <div className="previousOrdersList">
                    {table}
                </div>
                <div className="selectOrder">
                    <button className="waves-effect waves-light btn-large" onClick={(e)=>{this.props.copyOrder(this.props.fullOrder, 'fullOrder')}}>Select Order</button>
                </div>
            </div>
        )
    }
}

class CustomerSelected extends Component {
    constructor() {
        super();
        this.myRef = React.createRef() 
        this.state = {
            previousOrders : '',
            pageNumber : 0,
            prevPage : 0,
            nextPage : 1,
            isLoading : true,
        }
    }
    componentDidMount(){
        axios.get('/api/orders/' + this.props.customer.name + '/' + this.props.customer.phone + '/' + this.state.pageNumber * 5)
        .then((res) => {
            this.setState({
                previousOrders : res.data
            });
        })  
    }
    // MoveScrollbar = () => {
    //     window.addEventListener("scroll", function (event) {
    //         var scroll = this.scrollY;
    //         console.log('test 1', scroll)
    //     });
    // }
    fetchData = (newPage) => {
        axios.get('/api/orders/' + this.props.customer.name + '/' + this.props.customer.phone + '/' + newPage * 5)
        .then((res) => {
            this.setState({
                previousOrders : res.data,
                pageNumber : newPage,
            });
        }) 
    }
    nextPage = () => {
        var newPage = this.state.pageNumber;
        newPage++;
        // this.MoveScrollbar();
        if (this.state.previousOrders.length < 5) {
            return (
                <div className="nextBtn">
                    <button className="shaded"> <img src={arrow} alt='arrow'/></button>
                </div>
            )
        } else {
            return (
                <div className="nextBtn">
                    <button onClick={(e)=>{this.fetchData(newPage)}}><img src={arrow} alt='arrow'/></button>
                </div>
            )
        }
    }
    prevPage = () => {
        console.log('the current page : ',this.state.pageNumber);
        var newPage = this.state.pageNumber;
        if (newPage <= 0) {
            newPage = 0;
            return (
                <div className="prevBtn">
                    <button className="shaded"> <img src={arrow} alt='arrow'/></button>
                </div>
            )
        } else {
            newPage--;
            console.log('down page : ',newPage);
            return (
                <div className="prevBtn">
                    <button onClick={(e)=>{this.fetchData(newPage)}}> <img src={arrow} alt='arrow'/> </button>
                </div>
            )
        }
    }
    scrollToMyRef = () => { // run this method to execute scrolling. 
        window.scrollTo({
            left:this.myRef.current.offsetLeft, 
            behavior: "smooth"  // Optional, adds animation
        })
    }
    render () {
        // const table = [];
        // var orderCounter = 0;
        // var prevNum = this.state.pageNumber;
        // var nextNum = this.state.pageNumber;
        // var startID = "#start";
        // var containerID = "#previousWrapper";
        // var sLeft = startID.scrollLeft;
        // var reversed = false;
        if (this.state.previousOrders !== '') {
            const table = [];
            var sortedOrderNewsest = this.state.previousOrders;
            sortedOrderNewsest.forEach ( fullOrder => {
                table.push (
                    <DisplayPreviousOrders 
                        copyOrder={this.props.copyOrder}
                        key={uuidv1()}
                        fullOrder={fullOrder}
                    />
                )
            })
            // prevNum;
            // nextNum++;
            return (
                <div className="customerDisplay">
                    <div className="customerHeader">
                        <h1> {this.props.customer.name} </h1>
                        <h1> {this.props.customer.phone} </h1>
                        <h1> {this.props.customer.address} </h1>
                        <button onClick={(e) => {this.props.clearCustomer()}}> Clear </button>
                    </div>
                    <div className="previousOrderWrapper" ref={this.myRef} id="previousWrapper">
                        {this.prevPage()}
                        {table}
                        {this.nextPage()}
                    </div>
                </div>
            )
        } else {
            return (
                <div className="customerDisplay">
                    <div className="customerHeader">
                        <h1> {this.props.customer.name} </h1>
                    </div>
                    <div className="previousOrderWrapper" id="previousWrapper">
                        Create a new order!
                    </div>
                </div>
            )
        }
    }
}
