class CustomerDisplay extends Component {
    constructor(props) {
        super (props);
        this.state = {
            customers : [],
            onCustomerSelected : '',
            filterText: '',
            customerTool : 0,
            customerName : '',
            customerAddress : '',
            customerPhone : '',
            isInputValid : false
        };
        this.handleFilterTextChange = this.handleFilterTextChange.bind(this);
        this.handleOnClick = this.handleOnClick.bind(this);
    }
    componentDidMount() {
        var selectedCustomer = this.props.selectedCustomer;
        axios.get('/api/customers')
            .then((res) => {
                this.setState({customers : res.data})
            });
        
        this.setState ({
            onCustomerSelected : selectedCustomer
        })
    }
    refreshCustomerList = () => {
        axios.get('/api/customers')
            .then((res) => {
                this.setState({customers : res.data})
            });
    }
    onCustomerSelected = (customer) => {
        this.setState({
            onCustomerSelected : customer
        })
        var sendCustomer = customer;
        this.props.getCustomer(sendCustomer);
    }
    handleOnClick (change) {
        this.setState({
            customerTool : change
        })
    }
    handleFilterTextChange(filterText) {
         this.setState({
            filterText: filterText
        });
    }
    onCreateAccount = () => {
        const {
            customerName,
            customerAddress,
            customerPhone
        } = this.state;
        var sCustomerPhone = customerPhone.toString();
        axios.post ('/api/customers', {
            name : customerName,
            address : customerAddress,
            phone : sCustomerPhone
        })
        .then((res) => {
            this.onCustomerSelected (res.data)
        })
        .catch (function (error) {
        });
    }
    clearCustomer = () => {
        this.setState ({
            onCustomerSelected : ''
        })
        this.props.getCustomer('');
    }
    getCustomerTool = (tool) => {
        this.setState({
            customerTool : tool
        })
    }
    getName = (newName) => {
        var name = newName
        this.setState ({
            customerName : name
        })
    }
    getAddress = (newAddress) => {
        var address = newAddress
        this.setState ({
            customerAddress : address
        })
    }
    getPhone = (newPhone) => {
        var phone = newPhone
        this.setState ({
            customerPhone : phone
        })
    }
    isFullInput = (isValid) => {
        if (isValid && !this.state.isInputValid) {
            this.setState ({
                isInputValid : true,
            })
        } else if (!isValid && this.state.isInputValid) {
            this.setState ({
                isInputValid : false
            })
        }
    }
    render () {
        var renderCustomerTool = [];
        var whichToolOption = [];
        if (this.state.onCustomerSelected !== '') {
            return (
                <div className="customerDisplay">
                    <CustomerSelected 
                    customer = {this.state.onCustomerSelected} 
                    clearCustomer = {this.clearCustomer}
                    copyOrder = {this.props.copyOrder}
                    />
                </div>
            )
        } else {
            if (this.state.customerTool === 0) {
                renderCustomerTool.push (
                    <CustomerTable
                        customers={this.state.customers}
                        filterText={this.state.filterText}
                        onCustomerSelected = {this.onCustomerSelected} 
                        getCustomer={this.props.getCustomer} 
                        getCustomerTool = {this.customerTool}
                        key={this.customerTool + 1}
                    />
                )
                whichToolOption.push (
                    <SearchBar
                        filterText={this.state.filterText}
                        onFilterTextChange={this.handleFilterTextChange}
                        getCustomerTool = {this.customerTool}
                        key={this.customerTool + 3}
                    />
                )
            } else if (this.state.customerTool === 1) {
                if (this.state.isInputValid) {
                    whichToolOption.push (
                        <button className='createCustomerBtn validCreateBtn' onClick={(e) => {this.onCreateAccount()}}>Create Account</button>
                    )
                } else {
                    whichToolOption.push (
                        <button className='createCustomerBtn invalidCreateBtn'>Create Account</button>
                    )
                }
                renderCustomerTool.push( 
                    <CustomerAdd
                        handleAddedCustomer={this.props.handleAddedCustomer}
                        isFullInput={this.isFullInput}
                        getCusomterInfo = {this.getCusomterInfo}
                        getName = {this.getName}
                        getAddress = {this.getAddress}
                        getPhone = {this.getPhone}
                    />
                )
            }
            return (
                <div className="customerDisplay">
                    <div className="CustomerOptions">
                        <div className="customerTool">
                            <button className={(this.state.customerTool === 0) ? "activeTool" : 'inactiveTool'} onClick={(e) => {this.handleOnClick(0)}}> Customer Search </button>
                            <button className={(this.state.customerTool === 1) ? "activeTool" : 'inactiveTool'} onClick={(e) => {this.handleOnClick(1)}}> Customer Add </button>
                            {whichToolOption}
                        </div>
                    
                    </div>
                        <div className="CustomerWrapper">
                            {renderCustomerTool}
                        </div>
                </div>
            );
        }
    }
};

export default CustomerDisplay;