import React, { Component } from 'react';
import Analytics from './analytics';
import Users from './users';

import '../../style/Admin.css';

class Admin extends Component {
  constructor(props) {
    super(props);
    this.state = {
      token: '',
      signUpError: '',
      signInError: '',
      signInEmail: '',
      signInPassword: '',
      signUpEmail: '',
      signUpPassword: '',
      render : 'Users'
    };
    this.adminPanelNav = this.adminPanelNav.bind(this);
    this.displayAccountList = this.displayAccountList.bind(this);
  }
  displayAccountList () {
      return (
          <div className="Accounts">
            <div>

            </div>
          </div>
      )
  }
  adminPanelNav () {
      return (
        <div className="panelNav">
            <button className={this.state.render === 'Users' ? ' activePanel' : ' notActivePanel'} onClick={()=>{this.setState({ render : 'Users'})}}>
                Users
            </button>
            <button className={('Analytics ') + (this.state.render === 'Analytics' ? ' activePanel' : ' notActivePanel')}  onClick={()=>{this.setState({ render : 'Analytics'})}}>
                <p>Stats</p>
            </button>
        </div>
      )
  }
  adminRenderArea (render) {
      switch (render) {
          case "Users" : return <Users/>;
          case "Analytics" : return <Analytics/>;
          default : return <Users/>;
      }
  }
  render () {
      return (
        <div className="adminPanel">
            {this.adminPanelNav()}
            <div className="adminArea">
                {this.adminRenderArea(this.state.render)}
            </div>
        </div>
      )
  }
}

export default Admin;