import React, { Component } from 'react';
import axios from 'axios';

import MainNav      from './component/nav.js';
import LoginPage    from './component/loginPage/LoginPage.js';
import OrderPage    from './component/orderPage/OrderPage.js';
import AdminPage    from './component/admin/admin.js';

import './style/App.css';


// if (process.env.NODE_ENV !== 'production') {
//   const {whyDidYouUpdate} = require('why-did-you-update');
//   whyDidYouUpdate(React);
// }
const uuidv1 = require('uuid/v1');

class App extends Component {
	constructor() {
		super();
    	this.state = {
			users:[],
			userLogged : null,
			isUserLogged: false,
			isAdmin : false,
			server : true
    	};
  	}
	componentDidMount () {
		axios.get ('/api/users')
		.then ((res) => this.setState(
			{ users : res.data}
		))
		.catch ((error) => {
			console.error(error);
			this.setState ({
				server : false
			})
		});	
		setTimeout(() => this.setState({ loading: false }), 1500);
	}
	getUsers = () => {
		axios.get ('/api/users')
		.then (res => this.setState({ users : res.data}));
	}
	getUserLogged = (newUser) => {
		this.setState ({
			userLogged : newUser,
			isUserLogged : true
		})
	}
	getAdminLogged = (newUser) => {
		this.setState ({
			userLogged : newUser,
			isAdmin : true,
		})
	}
	handleLogOut = () => {
		this.setState ({
			isUserLogged : false,
			isAdmin : false,
			userLogged : null,
		})
		axios.get ('/api/users')
		.then (res => this.setState({ users : res.data}));
	}
	render () {
		var whichRender = [];
		// var checkUsers = this.state.users;
		// console.log('checking state here',this.state.users);
		if (typeof this.state.users === "undefined") {
			console.log('its null');
			// checkUsers = null;
		}
		if (!this.state.server) {
			console.log('server failed');
		}
		// if(loading) { // if your component doesn't have to wait for an async action, remove this block 
		// 	return null; // render null when app is not ready
	  	// }
		if (this.state.isUserLogged) {
			whichRender.push (
				<div className="AppContainer" key={this.state.userLogged.name}>
					<MainNav user={this.state.userLogged.name} handleLogOut = {this.handleLogOut}/>
					<OrderPage user={this.state.userLogged.name} handleLogOut = {this.handleLogOut}/>     
				</div>
			)
		} else if (this.state.isAdmin) {
			whichRender.push (
				<div className="AppContainer" key={this.state.userLogged.name}>
					<MainNav user={this.state.userLogged.name} handleLogOut = {this.handleLogOut}/>
					<AdminPage user={this.state.userLogged.name}/>     
				</div>
			)
		} else {
			whichRender.push (
				<div className="AppContainer" key={'loginPromptApp'}>
					<div className="loginPrompt">
						<LoginPage getUserLogged={this.getUserLogged} server = {this.state.server} users = {this.state.users} getAdminLogged = {this.getAdminLogged} reconnect = {this.getUsers}/>
					</div> 
				</div>
			)
		}
		const spinner = document.getElementById('spinner');

		if (spinner && !spinner.hasAttribute('hidden')) {
			spinner.setAttribute('hidden', 'true');
		}
		return (
			<div className="App">
				{whichRender}
			</div>
		)
	}
};

export default App;