import React, { Component } from 'react';
import axios from 'axios'
import UserInfo from './userInfo';

import '../../style/Admin.css';

class AccountRow extends Component {
    constructor(props) {
        super(props);
        this.state = {
            user : this.props.user,
            userInfo : false,
            allOrders : []
        }
        this.showUserInfo = this.showUserInfo.bind(this);
        this.closeUserInfo = this.closeUserInfo.bind(this);
        this.showDetails = this.showDetails.bind(this);
        this.displayType = this.displayType.bind(this);
    }
    showDetails() {
        var all = [];
        axios.get('/api/orders/' + this.state.user.name)
        .then(function (res) {
            console.log(res);
            res.data.forEach(e => {
                e.orders.forEach(each => {
                     all.push (
                        each
                    )
                })
            })
        })
        this.setState({
            allOrders : all
        })
    }
    closeUserInfo () {
        this.setState ({
            userInfo : false
        })
    }
    showUserInfo () {
        this.setState({ userInfo : true})
    }
    displayType() {
        var postAll = [];
        this.state.allOrders.forEach ( orda =>{
            postAll.push (
                <li> {orda.type}, {orda.extraType} </li>
            )
        })
        return postAll
    }
    render () {
        return (
            <tr className="accountInfo">
                <td className="accountFirst">{this.state.user.name}</td>
                <td className="accountPin">{this.state.user.pin}</td>
                <td><button className="viewUserInfo" onClick={(e) => {this.props.getUserInfo(this.state.user)}}> View User Info </button></td>
            </tr>
        );
    }
}

class ActiveTable extends Component {
    render () {
        const row = [];
        this.props.Users.forEach(user => {
            if (user.name !== "Nick" && user.pin !== 991212) {
                row.push (
                    <AccountRow
                        key={user._id}
                        user={user}
                        getUserInfo = {this.props.getUserInfo}
                    />
                )
            }
        });
        return (
            <table>
                <tbody>
                   
                    {row}
                </tbody>
            </table>
        )
    }
}

class CreateAccount extends Component {
    constructor(){
        super();
        this.state ={
            createName : '',
            createPin : '',
            createError : '',
            isUserDisplay : false,
        }
        this.onCreateAccount = this.onCreateAccount.bind(this);
    }
    onTextBoxChangeName = (e) => {
        this.setState({
            createName: e.target.value,
        });
    }
    onTextBoxChangePin = (e) => {
        this.setState({
            createPin : e.target.value,
        });
    }
    onCreateAccount() {
        if ( this.state.createName !== '' && this.checkPin(this.state.createPin)) {
            // var newPin = this.generatePin();
            const {
                createName,
                createPin,
            } = this.state;
            axios.post ('/api/users', {
                name : createName,
                pin : createPin
            })
            .then (res => {
                this.props.handleRefresh()
                console.log(res);
            }) 
            .catch (error => {
                console.log(error);
            });
        }
    }
    checkPin (newPin) {
        var checkPin = true;
        this.props.allPins.forEach(pin => {
            if (pin === newPin) {
                checkPin = false;
            }
        });
        return checkPin;
    }
    // generatePin () {
    //     var newPin = Math.floor(1000 + Math.random() * 9000)
    //     console.log(newPin);
    //     for (let i = 0; i < this.props.allPins.length; i++) {
    //         this.props.allPins.forEach(pin => {
    //             if (pin === newPin) {
    //                 newPin = Math.floor(1000 + Math.random() * 9000);
    //                 i = 0;
    //             }
    //         })
    //     }
    //     return newPin;
    // }
    render() {
        const createName = this.state.createName;
        const createPin = this.state.createPin;
        return (
            <div className="createAccount">
                <input 
                    type="name"
                    placeholder="User Name"
                    value={createName}
                    onChange={this.onTextBoxChangeName}
                />
                  <input 
                    type="number"
                    placeholder="1234"
                    value={createPin}
                    onChange={this.onTextBoxChangePin}
                />
                <button onClick={(e) => {this.onCreateAccount()}}>Create Account</button>
            </div>
        )
    }
}

class Users extends Component {
    constructor(){
        super();
        this.state = {
            Users: [],
            userInfo : null,
            userToDisplay: [],
            isUserDisplay : false,
            floatingDisplay: [],
            orders : []
        }
    }

    componentDidMount () {
        axios.get ('/api/users')
        .then (res => this.setState({ Users : res.data}));
    }
    handleRefresh = () => {
        axios.get ('/api/users')
        .then (res => this.setState({ Users : res.data}));
    }
    getUserInfo = (user) =>{
        this.setState({userToDisplay: user, isUserDisplay : true});
    }
    closeUserInfo = () => {
        this.setState ({
            userToDisplay : '',
            isUserDisplay : false,
            userInfo : null,
            floatingDisplay : []
        })
        this.handleRefresh();
    }
    gatherPins = () =>{
        var allPins = [];
        this.state.Users.forEach ( user => {
            allPins.push(
                user.pin
            )
        })
       return allPins
    }
    render () {
        var allPins = this.gatherPins();
        var displayUserInfo = [];
        if (this.state.isUserDisplay) {
            displayUserInfo.push (
                <UserInfo key={this.state.userToDisplay._id} user={this.state.userToDisplay} closeUserInfo={this.closeUserInfo} isUserDisplay={this.state.userToDisplay}/>
            )
        } else {
            displayUserInfo = [];
        };
        return (
            <div className="accountsRender"> 
                {displayUserInfo}
                <div className="createAccountsWrapper"> 
                    <h2> Create New Accounts </h2>
                    <div>
                        <CreateAccount users = {this.state.users} allPins = {allPins} handleRefresh={this.handleRefresh}/>
                    </div>
                </div>  
                <div className="accountsOptions">
                    <div className="activeAccounts">   
                        <div> 
                            <ActiveTable Users = {this.state.Users} getUserInfo = {this.getUserInfo}/>
                        </div>
                    </div>
                </div>
            </div>
        )
    }
}

export default Users;