import React, { Component } from 'react'; 
import axios from 'axios';

import '../../style/UserInfo.css';

class DetailsOrder extends Component {
    render () {
        var gatherMeat = [];
        var gatherVeggies = [];
        var gatherCheese = [];
        var gatherSauce = [];
        var toppingsRow = [];
        if (this.props.item.type === "Pizza" || this.props.item.type === "Sub" || this.props.item.type === "Panzerotti") {
            this.props.item.toppings.meat.forEach ((meat, i) =>  {
                gatherMeat.push (
                    <td key={i}>{meat}</td>
                )
            })
            this.props.item.toppings.veggies.forEach ((veggies,i) =>  {
                gatherVeggies.push (
                    <td key={i}>{veggies}</td>
                )
            })
            this.props.item.toppings.cheese.forEach ((cheese,i) =>  {
                gatherCheese.push (
                    <td key={i}>{cheese}</td>
                )
            })
            this.props.item.toppings.sauce.forEach ((sauce,i) =>  {
                gatherSauce.push (
                    <td key={i}>{sauce}</td>
                )
            })
            toppingsRow.push (
                <tr className='meatDetailed' key={'meat'}><td>Meat:</td>{gatherMeat}</tr>
            )
            toppingsRow.push (
                <tr className='veggiesDetailed' key={'veggies'}><td>Veggies:</td>{gatherVeggies}</tr>
            )
            toppingsRow.push (
                <tr className='cheeseDetailed' key={'cheese'}><td>Cheese:</td>{gatherCheese}</tr>
            )
            toppingsRow.push (
                <tr className='sauceDetailed' key={'sauce'}><td>Sauce:</td>{gatherSauce}</tr>
            )
        }
        var itemDisplay = this.props.item;
        return (
            <table>
                <tbody>
                        <tr>
                            <td>Date and Time</td>
                            <td>{itemDisplay.date}</td>
                        </tr>
                        <tr>
                            <td>Main:</td>
                            <td>{itemDisplay.type}</td>
                            
                        </tr>
                        <tr>
                            <td>Quantity:</td>
                            <td>{itemDisplay.quantity}</td>
                        </tr>
                        {toppingsRow}
                        <tr>
                            <td> Extra Item:</td>
                            <td>{itemDisplay.extraType}</td>
                        </tr>
                        <tr className="o_extraInfo">
                            <td>Extra Info:</td>
                            <td>{itemDisplay.extraInfo}</td>
                        </tr>
                </tbody>
            </table>
        );
    }
}

class DetailedOrderTable extends Component {
    render () {
        const table = [];
        this.props.order.forEach(item => {
            table.push (
                <DetailsOrder
                    key={item}
                    item={item}
                    isDisplayAllOrder={this.props.isDisplayAllOrder}
                />
            )
        });
        return (
            <div className="detailedItem">
                <div className="detailedOrderWrapper">
                    {table}
                </div>
            </div>
        )
    }
}

class OrderRow extends Component {
    render () {
        return (
            <tr>
                <td>{this.props.item.date}</td>
                <td>{this.props.item.customerName}</td>
                <td>{this.props.item.customerPhone}</td>
                <td>{this.props.item.orders.length}</td>
                <td>{this.props.item.orders[0].type}</td>
                <td><button className="viewOrder" onClick={(e) => this.props.viewOrderDetail(this.props.item.orders)}>View Order</button></td>
            </tr>
        );
    } 
}

class OrderTable extends Component {
    render () {
        const row = [];
        // console.log('this row ', row);
        this.props.order.forEach(item => {
            console.log('this row ', item);
            row.push (
                <OrderRow
                    key={item}
                    item={item}
                    viewOrderDetail={this.props.viewOrderDetail}
                    isDisplayAllOrder={this.props.isDisplayAllOrder}
                />
            )
        });
        return (
            <div className="allItems">
                <div className="allOrderHeader">
                    <ul>
                        <li>Date</li>
                        <li>Name</li>
                        <li>Phone Number</li>
                        <li>Item(s)</li>
                        <li>Order Main</li>
                    </ul>
                </div>
                <table className="allItemsTable">
                    <tbody>
                        {row}
                    </tbody>
                </table>
            </div>
        )
    }
}

class UserInfo extends Component {
    constructor(props){
        super(props);
        this.state = {
            orders: null,
            orderDetail : null,
            totalOrder : 0,
            orderLocation : 0,
            newOrderLocation: 0,
            pause : false,
            counter : 0,
            currentViewingNumber : 0,
            dateSearch : '',
            isRmoveUser : false,
            isDisplayAllOrder : false,
            isDiplayOrderDetails : false,
            page : 1,
        }
    }
    componentDidMount(){
        // var updateOrder = [];
        // var all = [];
        // var orderNumber = -1;
        // var tempCurrentViewingNumber = this.state.currentViewingNumber;
        axios.get('/api/orders/' + this.props.user.name + '/' + this.state.page * 10)
        .then((res) => {
            this.setState({
                isDisplayAllOrder : true,
                isDiplayOrderDetails : false,
                order : res.data
            });
        })      
        console.log('the order ', this.state.order);
    }
    onTextBoxSearchDate = (event) => {
        this.setState({
            dateSearch: event.target.value,
        });
    }
    onSearchDate = () => {
        const {
            dateSearch
        } = this.state;
        var all = [];
        axios.get('/api/orders/' + dateSearch)
        .then((res) => {
            res.data.forEach(e => {
                all.push (
                    e.orders
                )
            })
            this.setState({
                isDisplayAllOrder : true,
                isDiplayOrderDetails : false,
                orders : all
            })
        })
    }
    closeDetailedOrderTable = () => {
        this.setState({
            orderDetail : '',
            viewOrderDetail : false,
            isDisplayAllOrder : true,
            isDiplayOrderDetails : true
        })
    }
    removeUser = () => {
        this.setState ({
            isRmoveUser : true
        })
    }
    removeUserConfirm = () => {
        axios.delete('/api/users/' + this.props.user.name)
        .then ( function (res) {
            console.log(res);
        },  this.props.closeUserInfo())
    }
    viewOrderDetail = (order) => {
        this.setState({
            orderDetail : order,
            isDiplayOrderDetails : true,
            isDisplayAllOrder : false
        })
    }
    render () {
        var displayOrder = [];
        // var displayPrev = [];
        // var displayNext = [];
        // var isDisplayPrev = false;
        // var isDisplayNext = false
        var removeUserPopup = [];
        var closeType = [];
        const {
            dateSearch
        } = this.state;        
        if (this.state.isDisplayAllOrder) {
            displayOrder.push (
                <OrderTable order={this.state.order} key={this.state.order} viewOrderDetail={this.viewOrderDetail}/>
            )
             closeType.push (
                <div className="closeUserInfo" onClick = {(e)=> {this.props.closeUserInfo()}}>
                    <p>X</p>
                </div>
            )
        } else if (this.state.isDiplayOrderDetails && this.state.orderDetail !== null) {
            displayOrder.push (
                <DetailedOrderTable order={this.state.orderDetail} key={this.state.orderDetail}/>
            )
            closeType.push (
                <div className="closeUserInfo" onClick = {(e)=> {this.closeDetailedOrderTable()}}>
                    <p>Back</p>
                </div>
            )
        }
        if (this.state.isRmoveUser) {
            removeUserPopup.push (
                <div className="removeUser">
                    <h1>removing: {this.props.user.name}</h1>
                    <div className="removeOptions">
                        <button onClick={(e) => this.setState({isRmoveUser : false})}> Cancel </button>
                        <button onClick={(e) => this.removeUserConfirm()}> Confirm </button>
                    </div>
                </div>
            )
        } else {
            removeUserPopup = [];
        }
        return (
            <div className={this.props.isUserDisplay ? "userInfo" : 'hideUserInfo'}>
                <ul className="userInfoHeader">
                    <li><button className="removeUserBtn" onClick={(e) => {this.removeUser()}}> Remove User</button></li>
                    <li><h1>{this.props.user.name}</h1></li>
                        <li><input 
                            type="date"
                            placeholder="YYYY/MM/DD"
                            value={dateSearch}
                            onChange={this.onTextBoxSearchDate}
                        /></li>
                    <li><button className="searchDate" onClick={(e) => {this.onSearchDate()}}> Search</button></li>
                    <li>{closeType}</li>
                </ul>
                <div className="userInfoContainer">
                    <div className="userOrderList">
                        {displayOrder}
                    </div>
                </div>
                {removeUserPopup}
            </div>
        )
    }
}

export default UserInfo;    