import React, {Component} from 'react';

import mainLogo from '../../logo.png';

import '../../style/LoginPage.css';

class LoginPage extends Component {
    constructor(props)
    {
        super(props);
        this.state = {
            pinNumber : [],
            specialUser : false,
            show: false,
            showCorrect : false,
            filterText : '',
            pin : '',
        }
        this.handleClick = this.handleClick.bind(this);
        this.displayPin = this.displayPin.bind(this);
        this.clearPin = this.clearPin.bind(this);
    }
    componentDidMount () {
        this.nameInput.focus();
    }
    handleClick (pinPressed) {
        let updatePin = this.state.pinNumber;
        updatePin.push (
            pinPressed
        )
        this.setState({
            pinNumber : updatePin
        })
    }
    CheckConnection = () => {
        if (this.props.users.length <= 1) {
            console.log('we offline');
        }
        console.log('we online');
        return <p> we connected </p>
    }
    displayPin () {
        var pinBox = [];
        var maxPin = null;
        if (this.state.specialUser) {
            maxPin = 6
        } else {
            maxPin = 4
        }
        for (let i = 0; i < this.state.pinNumber.length; i++) {
            pinBox.push (
                <div className="pinBox" key={pinBox}>
                •
                </div>
            )
        }
        for (let y = this.state.pinNumber.length; y < maxPin ;y++) {
            pinBox.push(
                <div className="pinBox" key={pinBox}>
                </div>
            )
        }
        return pinBox;
    }
    clearPin() {
        this.setState ({
            show : true,
            pinNumber : []
        })
    }
    handleFilterTextChange = (e) => {
        this.onFilterTextChange(e.target.value);
    }
    checkPin = () => {
        var isCorrect = false;
        this.props.users.forEach (user => {
            if (user.pin.toString() === this.state.pin) {
                if (this.state.specialUser) {
                    this.props.getAdminLogged(user);
                } else {
                    this.props.getUserLogged(user);
                }
                isCorrect = true;
            } 
        });
        if (!isCorrect) {
            this.setState ({
                pin : ''
            })
        }
    }
    onFilterTextChange(filterText) {
        this.setState({
            pin : filterText
        })
    }
    handleImgClick = () => {
        if (this.state.specialUser) {
            this.setState ({
                specialUser : false
            })
        } else {
            this.setState ({
                specialUser : true
            })
        }
    }
    pinForm = () => {
        if (this.state.specialUser) {
            return (
                <div className="inputPass">
                    <form>
                        <input 
                            type="password" 
                            maxlength="6" 
                            ref={(input) => { this.nameInput = input; }} 
                            onChange={this.handleFilterTextChange}
                            value={this.state.pin}
                        >
                        </input>
                    </form>
                </div>
            )
        } else {
            return (
                <div className="inputPass">
                    <form>
                        <input 
                            type="number" 
                            ref={(input) => { this.nameInput = input; }} 
                            onChange={this.handleFilterTextChange}
                            value={this.state.pin}
                        >
                        </input>
                    </form>
                </div>
            )
        }
    }
    render() {
        if (this.state.pin.length === 4 && !this.state.specialUser) {
            this.checkPin();
        } else if (this.state.specialUser && this.state.pin.length === 6) {
            this.checkPin();
        }
        return (
            <div className="loginPage">
                <div className="login">
                    <div  className='pinContainer'>
                        <button onClick={(e)=>{this.handleImgClick()}}><img src={mainLogo}  className={this.state.specialUser ? 'activeSpecial' : 'inactiveSpecial' } alt='logo'/></button>
                        {this.pinForm()}
                    </div>
                </div>
            </div>
        );
    }
}


export default LoginPage;