        // } else {
        //     for (var i = 0; isDeal.cost.length > i; i++){
        //         displayOptions.push ( 
        //             <button onClick={(e)=> this.selectedDeal(isDeal,isDeal.size[i])}>{isDeal.size[i] + ' ' + isDeal.cost[i]}</button>
        //         )
        //         counter++;
