class Crust extends Component  {
    glutenFreeDough () {
        this.props.getCrust("glutentFree");
        this.props.getSize("medium");
    }
    isGlutenFree (crust,size) {
        if (crust === "glutentFree") {
            this.props.getSize("medium")
        } else {
            this.props.getSize(size);
        }
    }
    render () {
        var className = 'activeButton';
        return (
            <div className="crustSizeOptions">
                <div className="listFirstOptions"> 
                    <ul className="sizeOptions">
                        <li className="listHeader size"><img src={sizeIcon} alt="Size"/></li>
                        <li className="small"> <button className={(this.props.orderItem.size === "small") ? className : 'inactiveButton'} onClick={(e) => this.isGlutenFree(this.props.orderItem.crust, "small")}>Small </button></li>
                        <li className="medium"> <button className={(this.props.orderItem.size  === "medium") ? className : 'inactiveButton'} onClick={(e) => this.isGlutenFree(this.props.orderItem.crust,"medium")}>Medium </button></li>   
                        <li className="large"> <button className={(this.props.orderItem.size  === "large") ? className : 'inactiveButton'} onClick={(e) => this.isGlutenFree(this.props.orderItem.crust,"large")}>Large </button></li>
                        <li className="xlarge"> <button className={(this.props.orderItem.size === "extra large") ? className : 'inactiveButton'} onClick={(e) => this.isGlutenFree(this.props.orderItem.crust,"extra large")}>Extra Large </button></li>
                        <li className="party"> <button className={(this.props.orderItem.size === "party") ? className : 'inactiveButton'} onClick={(e) => this.isGlutenFree(this.props.orderItem.crust,"party")}>Party </button></li>
                    </ul>
                    <ul className="crustOptions">
                        <li className="listHeader"><img src={crustIcon} alt="Crust"/></li>
                        <li className="thin"><button className={(this.props.orderItem.crust === "thin") ? className : 'inactiveButton'} onClick={(e) => this.props.getCrust("thin")}>Thin</button></li>
                        <li className="normal"><button className={(this.props.orderItem.crust === "normal") ? className : 'inactiveButton'} onClick={(e) => this.props.getCrust("normal")}>Normal</button></li>   
                        <li className="thick"><button className={(this.props.orderItem.crust === "thick") ? className : 'inactiveButton'} onClick={(e) => this.props.getCrust("thick")}>Thick</button></li>
                        <li className="glutentFree"><button className={(this.props.orderItem.crust === "glutentFree") ? className : 'inactiveButton'} onClick={(e) => this.glutenFreeDough()}>Gluten Free Dough</button></li>
                    </ul>
                </div>
            </div>
        );
    }
}

class RenderToppings extends Component {
    constructor(props) {
        super (props);
        this.state = {
            toppings : {
                meat : ["Pepperoni","Ground Beef","Ham","Bacon","*Chicken","Italian Sausage","Hot Sausage","Salami","*Prosciutto","Anchovies"],
                veggies : ["Tomatoes","Hot Peppers","Onions","Mushrooms","Green Peppers","Pineapple","Green Olives","Black Olives","Red Peppers","Broccoli","Artichokes","Spinach","Sun Dried Tomatoes","Jalapeño","Roasted Red Pepper","Garlic"],
                cheese : ["Normal","Cheddar Cheese","*Double Cheese","Feta Cheese","Extra Cheese","*Goat Cheese"],
                sauce : ["House","Presto","BBQ", "Creamly Garlic"],
            },
            toppingsChosen : {
                meat : [],
                veggies : [],
                cheese : ["Normal"],
                sauce : ["House"],
            },
            toppingsChosenLeft : {
                meat : [],
                veggies : [],
                cheese : ["Normal"],
                sauce : ["House"],
            },
            toppingsChosenRight : {
                meat : [],
                veggies : [],
                cheese : ["Normal"],
                sauce : ["House"],
            }
        }
        this.handleToppingClicked = this.handleToppingClicked.bind(this);
        this.checkChosen = this.checkChosen.bind(this);
    }
    componentDidMount() {
        if (this.props.orderItem.split === 'na') {
            var copyToppings = this.props.orderItem.toppings;
            this.setState ({
                toppingsChosen : copyToppings
            })
        } else if (this.props.orderItem.split === 'left') {
            var copyToppingsLeft = this.props.orderItem.toppingsLeft;
            this.setState ({
                toppingsChosenLeft : copyToppingsLeft
            })
        } else if (this.props.orderItem.split === 'right') {
            var copyToppingsRight = this.props.orderItem.toppingsRight;
            this.setState ({
                toppingsChosenRight : copyToppingsRight
            })
        }
    }
    handleToppingClicked (toppingClicked) {
        var tempToppingChosen = this.state.toppingsChosen;
        var tempToppingChosenLeft = this.state.toppingsChosenLeft;
        var tempToppingChosenRight = this.state.toppingsChosenRight;
        if (typeof(this.checkChosen(toppingClicked)) === "undefined") {
            if (this.props.orderItem.split === 'na') {
                if (this.props.view === "meat") {
                    tempToppingChosen.meat.push (
                        toppingClicked
                    )
                } else if (this.props.view === "veggies") {
                    tempToppingChosen.veggies.push (
                        toppingClicked
                    )
                } else if (this.props.view === "cheese"){
                    tempToppingChosen.cheese.push(
                        toppingClicked
                    )
                } else if (this.props.view === "sauce"){
                    tempToppingChosen.sauce.push(
                        toppingClicked
                    )
                }
            } else if (this.props.orderItem.split === 'left') {
                if (this.props.view === "meat") {
                    tempToppingChosenLeft.meat.push (
                        toppingClicked
                    )
                } else if (this.props.view === "veggies") {
                    tempToppingChosenLeft.veggies.push (
                        toppingClicked
                    )
                } else if (this.props.view === "cheese"){
                    tempToppingChosenLeft.cheese.push(
                        toppingClicked
                    )
                } else if (this.props.view === "sauce"){
                    tempToppingChosenLeft.sauce.push(
                        toppingClicked
                    )
                }
            } else if (this.props.orderItem.split === 'right') {
                if (this.props.view === "meat") {
                    tempToppingChosenRight.meat.push (
                        toppingClicked
                    )
                } else if (this.props.view === "veggies") {
                    tempToppingChosenRight.veggies.push (
                        toppingClicked
                    )
                } else if (this.props.view === "cheese"){
                    tempToppingChosenRight.cheese.push(
                        toppingClicked
                    )
                } else if (this.props.view === "sauce"){
                    tempToppingChosenRight.sauce.push(
                        toppingClicked
                    )
                }
            }
          
            if (this.props.orderItem.split === 'na') {
                this.setState ({
                    toppingsChosen : tempToppingChosen
                })
            } else if (this.props.orderItem.split === 'left') {
                this.setState ({
                    toppingsChosenLeft : tempToppingChosenLeft
                })
            } else if (this.props.orderItem.split === 'right') {
                this.setState ({
                    toppingsChosenRight : tempToppingChosenRight
                })
            }
        } else {
            this.removeTopping(toppingClicked); 
        }
        if (this.props.orderItem.split === 'na') {
            this.props.getToppings(this.state.toppingsChosen);
        } else if (this.props.orderItem.split === 'left') {
            this.props.getToppings(this.state.toppingsChosenLeft);
        } else if (this.props.orderItem.split === 'right') {
            this.props.getToppings(this.state.toppingsChosenRight);
        } 
    }
    checkChosen (topping) {
        let chosenTopping = [];
        if (this.props.orderItem.split === 'na'){
            if (this.props.view === "meat") {
                chosenTopping = this.props.orderItem.toppings.meat;
            } else if (this.props.view === "veggies") {
                chosenTopping = this.props.orderItem.toppings.veggies;
            } else if (this.props.view === "cheese") {
                chosenTopping = this.props.orderItem.toppings.cheese;
            } else if (this.props.view === "sauce") {
                chosenTopping = this.props.orderItem.toppings.sauce;
            }
        } else if (this.props.orderItem.split === 'left') {
            console.log('we left');
            if (this.props.view === "meat") {
                chosenTopping = this.props.orderItem.toppingsLeft.meat;
                console.log('the left meat toppings: ', chosenTopping);
            } else if (this.props.view === "veggies") {
                chosenTopping = this.props.orderItem.toppingsLeft.veggies;
            } else if (this.props.view === "cheese") {
                chosenTopping = this.props.orderItem.toppingsLeft.cheese;
            } else if (this.props.view === "sauce") {
                chosenTopping = this.props.orderItem.toppingsLeft.sauce;
            }
        } else if (this.props.orderItem.split === 'right') {
            console.log('we right');
            if (this.props.view === "meat") {
                chosenTopping = this.props.orderItem.toppingsRight.meat;
                console.log('the right meat toppings: ', chosenTopping);
            } else if (this.props.view === "veggies") {
                chosenTopping = this.props.orderItem.toppingsRight.veggies;
            } else if (this.props.view === "cheese") {
                chosenTopping = this.props.orderItem.toppingsRight.cheese;
            } else if (this.props.view === "sauce") {
                chosenTopping = this.props.orderItem.toppingsRight.sauce;
            }
        }
        for (var i = -1; i < chosenTopping.length; i++) {
            if (topping === chosenTopping[i]) {
                return chosenTopping[i];
            } 
        } 
    }
    removeTopping (topping) {
        var tempToppingChosen = [];
        if (this.props.orderItem.split === 'na'){
            tempToppingChosen = this.props.orderItem.toppings;
        } else if (this.props.orderItem.split === 'left') {
            tempToppingChosen = this.props.orderItem.toppingsLeft;
        } else if (this.props.orderItem.split === 'right') { 
            tempToppingChosen = this.props.orderItem.toppingsRight;
        }
        if (this.props.view === "meat") {
            for (let i = 0; i < tempToppingChosen.meat.length; i++) {
                if (topping === tempToppingChosen.meat[i]) {
                    tempToppingChosen.meat.splice(i,1);
                    this.setState ({
                        toppingsChosen : tempToppingChosen
                    })
                }
            }
        } else if (this.props.view === "veggies") {
            for (let i = 0; i < tempToppingChosen.veggies.length; i++) {
                if (topping === tempToppingChosen.veggies[i]) {
                    tempToppingChosen.veggies.splice(i,1);
                    this.setState ({
                        toppingsChosen : tempToppingChosen
                    })
                }
            }
        } else if (this.props.view === "cheese") {
            for (let i = 0; i < tempToppingChosen.cheese.length; i++) {
                if (topping === tempToppingChosen.cheese[i]) {
                    tempToppingChosen.cheese.splice(i,1);
                    this.setState ({
                        toppingsChosen : tempToppingChosen
                    })
                }
            }
        } else if (this.props.view === "sauce") {
            for (let i = 0; i < tempToppingChosen.sauce.length; i++) {
                if (topping === tempToppingChosen.sauce[i]) {
                    tempToppingChosen.sauce.splice(i,1);
                    this.setState ({
                        toppingsChosen : tempToppingChosen
                    })
                }
            }
        }
    }
    render() {
        var toppingsView = [];
        var toppingsList = [];
        var classNameList = "";
        if (this.props.view === "meat") {
            toppingsView = this.state.toppings.meat;
        } else if ( this.props.view === "veggies") {
            toppingsView = this.state.toppings.veggies;
        } else if ( this.props.view === "cheese") { 
            toppingsView = this.state.toppings.cheese;
        } else if ( this.props.view === "sauce") {
            toppingsView = this.state.toppings.sauce;
        }
        toppingsView.forEach((topping, index) => {
            toppingsList.push (
                <li key={topping} className={(this.checkChosen(topping) === topping) ? 'toppingChosen' : 'toppingNotChosen'} onClick={(e)=>{this.handleToppingClicked(topping)}}><p>{topping}</p></li>
            )
        })
        if (this.props.view === "veggies") {
            classNameList = "veggiesList";
        } else if ( this.props.view === "cheese") {
            classNameList = "cheeseList";
        } else if (this.props.view === "sauce") {
            classNameList = "sauceList";
        } else {
            classNameList = "toppingsList";
        }
        return (
            <ul className={classNameList}>{toppingsList}</ul>
        )
    }
}

class ToppingsSplit extends Component {
    constructor(){
        super();
        this.state = {
            view : "meat",
            split : 'na'
        };
    } 
    handleChangeView = (newView) => {
        this.setState ({
            view : newView
        })
    }
    renderToppings(view) {
        switch (view){
            case "meat" : return <RenderToppings view = "meat" getToppings = {this.props.getToppings} orderItem = {this.props.orderItem} whichToppings = {this.props.orderItem}/>
            case "veggies" : return <RenderToppings view = "veggies" getToppings = {this.props.getToppings} orderItem = {this.props.orderItem}  whichToppings = {this.props.orderItem}/>
            case "cheese" : return <RenderToppings view = "cheese" getToppings = {this.props.getToppings} orderItem = {this.props.orderItem}  whichToppings = {this.props.orderItem}/>
            case "sauce" : return <RenderToppings view = "sauce" getToppings = {this.props.getToppings} orderItem ={this.props.orderItem}  whichToppings = {this.props.orderItem}/>
            default : return <RenderToppings view = "meat" getToppings = {this.props.getToppings} orderItem = {this.props.orderItem}  whichToppings = {this.props.orderItem}/>
        }
    }
    render () {
        var className = "toppingsActive";
        var displayToppingsLeft = [];
        var displayToppingsRight = [];
        if (this.props.orderItem.type === 'Split Pizza') {
            displayToppingsLeft.push (
                <li className={(this.props.orderItem.split === "left") ? 'toppingsLeft ActiveSide' : 'toppingsLeft InactiveSide'} onClick={(e) => this.props.getSplit("left")}>
                    <img src={leftSideIcon} alt="Left Side"/>
                    
                </li>
            )
            displayToppingsRight.push (
                <li className={(this.props.orderItem.split === "right") ? 'toppingsRight ActiveSide' : 'toppingsRight InactiveSide'} onClick={(e) => this.props.getSplit("right")}>
                    <img src={leftSideIcon} alt="Right Side"/>
                </li>
            )
        }
        return (
            <div className="toppingContainer">
                <div className="toppingNav">
                    <ul>
                        {displayToppingsLeft}
                        <li className={(this.state.view === "meat") ? className : 'toppingsInactive'} onClick={(e) => this.handleChangeView("meat")}>
                            <img src={meatIcon} alt="Meat"/>
                        </li>
                        <li className={(this.state.view === "veggies") ? className : 'toppingsInactive'} onClick={(e) => this.handleChangeView("veggies")}>
                            <img src={veggiesIcon} alt="Veggies"/>
                        </li>
                        <li className={(this.state.view === "cheese") ? className : 'toppingsInactive'} onClick={(e) => this.handleChangeView("cheese")}>
                            <img src={cheeseIcon} alt="Cheese"/>
                        </li>
                        <li className={(this.state.view === "sauce") ? className : 'toppingsInactive'} onClick={(e) => this.handleChangeView("sauce")}>
                            <img src={sauceIcon} alt="Sauce"/>
                        </li>
                        {displayToppingsRight}
                    </ul>
                </div>
                <div className="toppingsBox">
                    {this.renderToppings(this.state.view)}
                </div>
            </div>
        );
    }
}

