import React, {Component} from 'react';

export default class Slices extends Component {
    constructor (props) {
        super(props);
        this.state = {
            slices : [
                "Canadian",
                "Hawaiian",
                "Meat Lover",
                "Delux",
                "Vegetarian"
            ]
        }
        this.displaySlices = this.displaySlices.bind(this);
        this.addSlice = this.addSlice.bind(this);
    }
    addSlice (slice) {
        this.props.orderItem.type = slice + " Slice";
        this.props.orderItem.cost = 0.00;
        this.props.addToOrder(this.props.orderItem,true);
    }
    displaySlices (){
        var displayAllSlices = [];
        this.state.slices.forEach ( slice => {
            displayAllSlices.push (
                <li key={slice} onClick={(e)=>{this.addSlice(slice)}}> 
                    <p>{slice}</p>
                </li>
            )
        })
        return (
            <ul>
                {displayAllSlices}
            </ul>
        )

    }
    render() {
        return (
            <div className="displaySlices">
                {this.displaySlices()}
            </div>
        )
    }
}